.back-to-top {
    transition: background-color .3s,
        opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    width: 52px;
    height: 52px;
    background: var(--primary);
    position: fixed;
    bottom: 30px;
    right: 16px;
    cursor: pointer;
    border-radius: 99px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.20);

    &::before {
        position: absolute;
        content: "\e908";
        font-family: "icomoon";
        color: #fff;
        font-size: 28px;
        font-weight: $font-weight-normal;
    }
    
    // &:hover {
    //     border: 1px solid var(--primary);
    //     background: #fff;

    //     &::before {
    //         color: var(--primary);
    //     }
    // }

    &.show {
        opacity: 1;
        visibility: visible;
        z-index: 999;
    }
}